<template>
	<aside>
		<ul class="pg_component_box">
			<li v-for='(item,index) in values' :class="index | getClassName(values,step)" style="flex:1">
				<div class="pg_component_title">{{item}}</div>
				<div class="pg_component_bg"><span class="pg_compomemt_left"></span><i></i><span></span></div>
			</li>
			<!-- <li class="pg_component_l pg_component_l_b" style="flex:1">
				<div class="pg_component_title">验证</div>
				<div class="pg_component_bg"><i></i><span></span></div>
			</li>
			<li class="pg_component_l pg_component_l_m" style="flex:2">
				<div class="pg_component_title">验证</div>
				<div class="pg_component_bg"><i></i><span></span></div>
			</li>
			<li class="pg_component_l pg_component_l_c" style="flex:1">
				<div class="pg_component_title">验证</div>
				<div class="pg_component_bg"><i></i><span></span></div>
			</li> -->
		</ul>
	</aside>
</template>

<script>
	export default {
		props:{
			step:{
				type:Number,
				default:0
			},
			values:{
				type:Array,
				default:function(){
					return ['验证','成功']
				}
			}
		},
		data(){
			return {

			}
		},
		filters:{
			getClassName(index,opt,num){
				let l = opt.length,
					str = 'pg_component_l';
				if(index === 0){
					str += ' pg_component_l_b'
				}else if(index === (l-1)){
					str += ' pg_component_l_c'
				}else{
					str += ' pg_component_l_m'
				}

				if(num !== 0 && num >= index){
					if(num === index){
						str += ' pg_component_l_ac'
					}else{
						str += ' pg_component_l_active'
					}
				};
				return str;
			}
		}
	}
</script>

<style scoped>
	.pg_component_box{
		display:flex;
		color:#717989;
	}
	.pg_component_l{
		flex:1;
	}
	.pg_component_l_acitve{
		color:#51d1b1;
	}
	.pg_component_bg{
		position:relative;
		height:9px;
		overflow:hidden;
	}
	.pg_component_l i{
		position:absolute;
		width:9px;
		height:9px;
		border-radius:50%;
		background:#12141d;
	}
	.pg_component_l_b{
		color:#51d1b1;
	}
	.pg_component_l_b i{
		background:#51d1b1;
	}
	.pg_component_l_b i{
		top:0;
		left:0;
	}
	.pg_component_l_m i{
		top:0;
		left:0;
		bottom:0;
		right:0;
		margin:0 auto;
	}
	.pg_component_l_c i{
		top:0;
		right:0;
	}
	.pg_component_l_c .pg_component_title{
		text-align:right;
	}
	.pg_component_l_m .pg_component_title{
		text-align:center;
	}
	.pg_component_bg span{
		display:block;
		height:5px;
		margin-top:2px;
		background:#12141d;
	}
	.pg_component_l_m .pg_component_bg span{
		float:left;
		width:50%;
	}
	.pg_component_bg{
		margin-top:4px;
	}
	.pg_component_l_active span{
		background:#51d1b1;
	}
	.pg_component_l_active i,.pg_component_l_ac i{
		background:#51d1b1;
	}
	.pg_component_l_active,.pg_component_l_ac{
		color:#51d1b1;
	}
	.pg_component_l_ac .pg_compomemt_left{
		background:#51d1b1;
	}

	
</style>


