<template>
	<section>
		<Progressbar :step="0" style="width:90%;margin:0 auto;padding:10px"></Progressbar>
		<div class="re_pic_box">
			<img src="" alt="">
		</div>
		<div v-if="!re_flag" class="re_article_border">
			<article class="re_yzm_article">
				<p>请输入20位门票识别码</p>
				<div class="re_yzm_box c">
					<input type="text">
					<button type="button" v-btnClickEffect @click="verificationHandler">验证</button>
				</div>
				<dl>
					<dt>温馨提示</dt>
					<dd>1.输入识别码时请注意数字0、字母o的区别。</dd>
					<dd>2.官方不会以任何形式向您索要识别码，请勿告知任何人。</dd>
					<dd>3.邀请函请在开赛前及时使用，以免因过期造成您的损失。</dd>
				</dl>		
			</article>
		</div>
		<div v-else class="re_article_border">
			<article class="re_success_article">
				<h2>验证通过</h2>
				<p>恭喜您 <span>刘海涛</span></p>
				<p class="re_art_c">您已成功报名<span>WPT National China 中国（三亚）扑克游戏锦标赛</span></p>
				<p class="re_next_tips">专属检录牌已生成，请使用<span>电子检录牌</span>和<span>身份证件</span>配合现场检录，点击下一步查看</p>
				<ul class="re_art_tips">
					<h3>温馨提示：</h3>
					<li>1.电子凭证一经检录立即失效，请勿将电子凭证信息泄露给任何人。</li>
					<li>2.任何原因导致的迟到将会错失比赛资格，请您尽量提前抵达现场进行检录。</li>
					<li>3.遇到任何问题请及时求助现场工作人员，请勿相信他人提供的任何信息。</li>
					<li>4.本次赛事最终解释权归联众公司所有。</li>
				</ul>
			</article>
		</div>
		<button v-if="re_flag" v-btnClickEffect @click="nextHandler" type="button" class="re_next_btn">下一步</button>
	</section>
</template>

<script>
	import Progressbar from '@/components/progressbar';
	export default {
		data(){
			return {
				re_flag:false
			}
		},
		methods:{
			verificationHandler(){
				this.re_flag = true;
			},
			nextHandler(){
				this.$store.commit('changeShowCheckcard',{
					value:true
				})
				this.$router.push({
					name:'schedule'
				});
			}
		},
		components:{
			Progressbar
		},
		directives:{
			btnClickEffect:{
				bind(el){
					el.addEventListener('touchstart',function(){
						this.style.background = "rgba(81,225,209,.9)"
					})
					el.addEventListener('touchend',function(){
						this.style.background = "rgba(81,225,193,1)"
					})
				}
			}
		}

	}
</script>

<style scoped lang='scss'>
	@import './../assets/sass/function.scss';
	section{
		padding-bottom:30px;
	}
	.re_pic_box{
		width:px2em(640px);
		height:px2em(270px);
		margin:0 auto 20px;
		background:#151921;
		border:2px solid #30353f;
	}
	.re_article_border{
		padding-top:20px;
		border-top:1px solid #2f3647;
	}
	article{
		margin:0 auto;
		color:#717989;
		font-size:px2em(24px);
	}
	.re_yzm_article{
		width:px2em(680px);
	}
	.re_success_article{
		width:px2em(670px);
		text-align:center;
		font-size:px2em(20px);
		h2{
			font-size:px2em(28px);
			color:#51d1b1;
			line-height:2.5;
		}
		p{
			line-height:1.5
		}
		.re_art_c{
			width:px2em(530px);
			margin:0 auto;
			padding:5px 0;
			font-size:px2em(24px);
			span{
				color:#efc261;
			}
		}
	}
	.re_yzm_box{
		margin:5px 0;
		input{
			height:30px;
			line-height:30px;
			width:px2em(460px);
			outline:none;
			border:none;
			background:#151921;
			color:#ffffff;
			border-radius:3px;
			text-align:center;
			float:left;
		}
		button{
			float:left;
			width:px2em(180px);
			height:30px;
			margin-left:px2em(30px);
			padding:0;
			line-height:30px;
			border:none;
			verticle-align:middle;
			outline:none;
			border-radius:3px;
			background:#51d1b1;
			color:#ffffff;
			text-align:center;
		}
	}
	dd{
		font-size:px2em(20px);
	}
	.re_next_btn{
		position:fixed;
		bottom:0;
		left:0;
		width:100%;
		height:50px;
		line-height:50px;;
		padding:0;
		font-size:px2em(36px);
		color:#ffffff;
		background:#51d1b1;
		border:none;
		line-height:none;
		outline:none;
	}
	.re_next_tips{
		padding:50px 0;
		span{
			color:#efc261;
		}
	}
	.re_art_tips{
		text-align:left;
	}
</style>


